import React from "react";
import { Link } from "react-router-dom";
export function Header(){
    return (
        <div>
        <header id="header">

<nav data-aos="zoom-out" data-aos-delay="800" class="navbar navbar-expand">
    <div class="container header">
        <a class="navbar-brand" href="index.html">
            <img class="navbar-brand-sticky" src="assets/img/logo/logo.png" alt="sticky brand-logo" />
        </a>
        <div class="ml-auto"></div>
        <ul class="navbar-nav items mx-auto">
            <li class="nav-item dropdown">
                <Link className="nav-link"  to="/">Home</Link>
            </li>
            
            <li class="nav-item">
                <Link className="nav-link" to="/auction" >Auction</Link>
            </li>

            <li class="nav-item">
                <Link className="nav-link" to="/NFT">NFTs</Link>
            </li>
            
            <li class="nav-item">
                <Link className="nav-link" to="/contact">Contact Us</Link>
            </li>
        </ul>
        
        <ul class="navbar-nav icons">
            <li class="nav-item">
                <a href="#" class="nav-link" data-toggle="modal" data-target="#search">
                    <i class="fas fa-search"></i>
                </a>
            </li>
        </ul>

        
        <ul class="navbar-nav toggle">
            <li class="nav-item">
                <a href="#" class="nav-link" data-toggle="modal" data-target="#menu">
                    <i class="fas fa-bars toggle-icon m-0"></i>
                </a>
            </li>
        </ul>

        
        <ul class="navbar-nav action">
            <li class="nav-item ml-3">
                <a href="wallet-connect.html" class="btn ml-lg-auto btn-bordered-white"><i class="icon-wallet mr-md-2"></i>Wallet Connect</a>
            </li>
        </ul>
    </div>
</nav>
</header>      
        </div>
    )    
 }